import { Form, Input, Row, Col, Button } from 'antd'
import { useDispatch } from 'react-redux'

import { _changeLogForm } from '../../../redux/actions/logFormActions'

const ForgotForm = (props) => {
  const dispatch = useDispatch()

  const handleLogIn = (e) => {
    dispatch(_changeLogForm('login'))
  }

  const handleRegister = (e) => {
    dispatch(_changeLogForm('register'))
  }

  return (
    <Form
      name="forgot"
      labelCol={{ span: 6 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      autoComplete="off"
    >
      <Form.Item
        label="Tn ng nhp"
        name="username"
        rules={[{ required: true, message: 'Vui lng nhp tn ng nhp!' }]}
      >
        <Input
          allowClear={true}
          onChange={(e) =>
            dispatch({
              type: 'CHANGE_FORM',
              payload: {
                username: e.target.value.trim().toLowerCase()
              }
            })
          }
        />
      </Form.Item>

      <Row style={{ textAlign: 'center' }}>
        <Col span={12}>
          <Button type="link" onClick={handleLogIn}>
             c ti khon?
          </Button>
        </Col>
        <Col span={12}>
          <Button type="link" onClick={handleRegister}>
            ng k
          </Button>
        </Col>
      </Row>

      <Form.Item wrapperCol={{ offset: 8, span: 16 }} style={{ margin: '10px' }}>
        <Button
          size="large"
          shape="round"
          style={{
            textAlign: 'center',
            background: 'orange',
            color: 'white',
            fontSize: '130%',
            height: '50px',
            width: '50%'
          }}
        >
          {' '}
          Ly mt khu
        </Button>
      </Form.Item>
    </Form>
  )
}

export default ForgotForm
